import React, { useState, useEffect } from "react";
import Nav from "./Nav";
import "./App.css";
import { setTokenSourceMapRange } from "typescript";



export default function App() {
  const [disApp, setDisApp] = useState('none')
  const [disLogin, setDisLogin] = useState('block')

  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [vlidationMess, setValidationMess] = useState('')
  const [token, setToken] = useState('')


  const login = () => {
    var validRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

    if (email.length == 0) {
      setValidationMess(
        <div class="alert alert-danger" role="alert">
          Please enter a email.
        </div>
      )
      setTimeout(() => { setValidationMess(''); }, 2000);

    }
    else if (!email.match(validRegex)) {
      setValidationMess(
        <div class="alert alert-danger" role="alert">
          Please Enter a valid email
        </div>
      )
      setTimeout(() => { setValidationMess(''); }, 2000);

    }
    else if (password.length == 0) {
      setValidationMess(
        <div class="alert alert-danger" role="alert">
          Please enter your password.
        </div>
      )
      setTimeout(() => { setValidationMess(''); }, 2000);
    }
    else {
      fetch("http://192.168.143.9:8080/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email: email, password: password })
      })
        .then(res => res.json())
        .then((res) => {
          if (res.msg == "true") {
            setToken(res.token)
            setDisLogin('none')
            setDisApp('block')
          }
        })
    }
  }


  return (
    <div className="container-fluid">
      <div className="main-app" style={{ display: disApp }}>
        <Nav token={token} />
      </div>

      <div className="login" style={{ display: disLogin }}>
        <div className="login-box">
          <img src="img/main-logo.png" style={{ marginLeft: 120 }} width="50px" /><br />
          {vlidationMess}
          <label>Email</label>
          <input type="email" className="form-control" value={email} onChange={event => setEmail(event.target.value)} /><br />
          <label>Password</label>
          <input type="password" className="form-control" value={password} onChange={event => setPassword(event.target.value)} /><br />
          <button style={{ marginLeft: 120 }} onClick={login} className="btn btn-light">Login</button>
        </div>
      </div>

    </div>
  );
}

