import React, { Component, useState,useEffect } from "react";
import { FaBars } from "react-icons/fa";
import { BsFillPersonFill } from "react-icons/bs";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import Chatorey from './Category.js'
import AddStories from './Stories.js'


export default function Nav({token}) {
  const [width, setWidth] = useState(0);

  const openMenu = () => {
    setWidth(250);
  };
  const close = () => {
    setWidth(0);
  };

  const test = () => {
    console.log("test");
  };

  /* contains all cat records from database */
  const [category,setCategory] = useState([])

  const getData = () => {
    fetch("http://192.168.143.9:8080/category")
      .then(res => res.json())
      .then(res => {
        console.log(res);
        setCategory(res)
      })
  };

  useEffect(() => {
    getData();
  }, []);


  return (
    <div>
      <nav className="navbar navbar-dark bg-dark">
        <a className="navbar-brand" href="#">
          <img src="img/main-logo.png" width="23px" />{" "}
        </a>
        <button className="barsIcon" onClick={openMenu}>
          <FaBars />
        </button>
      </nav>
      <Router>
        <div id="mySidenav" class="sidenav" style={{ width: width }}>
          <div className="dropdown" >
            <button
              className="btn btn-secondary dropdown-toggle"
              type="button"
              id="dropdownMenuButton"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            >
              <BsFillPersonFill />  Hi Admin
            </button>
            <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
            <Link to="/add-chatorey" onClick={close}>Manage Category</Link>
            <Link to="/add-stories" onClick={close}>Manage Stories</Link>
            </div>
          </div>

          <button className="closebtn" onClick={close}>
            &times;
          </button>

          <Link to="/" onClick={(test, close)}>
            All
          </Link>
          <Link to="/about">About</Link>
          <Link to="/users">Users</Link>
          {
            category.map(c => {
              return(
                <Link to={c.name}>{c.name}</Link>
              )
            })
          }
        </div>

        <Switch>
          <Route path="/about">
            <h1>About</h1>
          </Route>
          <Route path="/users">
            <h1>Users</h1>
          </Route>
          <Route path="/add-chatorey">
            <Chatorey category = {category} getData={getData} />
          </Route>
          <Route path="/add-stories">
            <AddStories />
          </Route>
          
          <Route path="/">
            <h1>All News</h1>
          </Route>
         
        </Switch>
      </Router>
    </div>
  );
}
